import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
// import LoadingMore from '@src/mobile/components/LoadingMore';
import Tloader from '@src/mobile/components/Tloader';
import EventList from '@src/mobile/components/EventList';
import { EVENT_LIST_PAGE } from '@src/public/constants';
import { changeEventList, changeEventListType } from '../utils';
import { ListStore } from '../store';
import styles from './index.css';
import Filter from './Filter';
import Spin from './Spin';
import NoData from './NoData';

const Main = () => {
    const listStore = ListStore.useContainer();
    const { listStates, changeListStates } = listStore;
    const {
        qrcode,
        listLoading,
        id,
        page,
        size,
        party,
        highlights,
        order,
        total,
        nodes,
        allFold,
        parties,
        title,
        allowComment,
        summary,
        shareImgUrl,
    } = listStates;

    const refreshData = async (resolve, reject, reset) => {
        window.timelineReset = reset;

        try {
            await changeEventList({
                query: {
                    id,
                    page: 1,
                    size,
                    party,
                    highlights,
                    order,
                },
                type: changeEventListType.refresh,
                changeData: changeListStates,
            });

            resolve();
        } catch (e) {
            console.error(e);
            reject();
        }
    };

    const loadMoreData = async () => {
        let statusText = 'error';

        try {
            if ((page - 1) * size < total) {
                await changeEventList({
                    query: {
                        id,
                        page,
                        size,
                        party,
                        highlights,
                        order,
                    },
                    type: changeEventListType.more,
                    changeData: changeListStates,
                    prevNodes: nodes,
                });

                statusText = 'success';
            } else {
                statusText = 'end';
            }
        } catch (e) {
            console.error(e);
        }

        return statusText;
    };

    return (
        <div className={styles.wrap}>
            <Filter />
            <Tloader onRefresh={refreshData}>
                {!listLoading && nodes.length > 0 ? (
                    <>
                        <div className={styles.listWrap}>
                            <EventList
                                content={{
                                    nodes,
                                    parties,
                                    timeline: {
                                        id,
                                        title,
                                        allowComment,
                                        summary,
                                        qrcode,
                                        shareImgUrl,
                                    },
                                }}
                                showType={EVENT_LIST_PAGE}
                                allFold={allFold}
                                changeAllFold={bool => {
                                    changeListStates({ allFold: bool });
                                }}
                            />
                        </div>
                        {/* <LoadingMore control={loadMoreData} /> */}
                    </>
                ) : null}
            </Tloader>
            {listLoading ? <Spin /> : <>{nodes.length > 0 ? null : <NoData />}</>}
        </div>
    );
};

export default errorBoundary(Main);
