import React, { useEffect, useRef, useState } from 'react';
import classNames from 'classnames/bind';
import errorBoundary from '@src/components/errorBoundary';
import { dialog, message, Sheet } from '@src/mobile/components/ModalBox';
import { groundsClose, isGrounds, userIsLogin, userLogin } from '@src/public/utils/app';
import { useMaximize } from '@src/public/hooks/app';
import { smoothScroll } from '@src/public/utils';
import { useFixedTitle } from './hooks';
import { cancelFollowTimeline, followTimeline } from '../service';
import { ListStore } from '../store';
import styles from './index.css';
import More from './More';
import StatusBar from '@src/mobile/components/StatusBar';

const cx = classNames.bind(styles);

const Nav = () => {
    const listStore = ListStore.useContainer();
    const { listStates, changeListStates } = listStore;
    const { id, title, follow } = listStates;

    const statusBarHeight = useMaximize();

    const handleFollowClick = async () => {
        // 端内且未登录的情况下点击登录
        if (isGrounds && !userIsLogin()) {
            userLogin();

            return;
        }

        if (follow) {
            dialog({
                content: '取消追踪，无法跟进时间更新',
                okText: '不了吧',
                cancelText: '取消追踪',
                onCancel: async () => {
                    try {
                        await cancelFollowTimeline(id);
                        changeListStates({ follow: false });
                        message({ content: '已取消追踪' });
                    } catch (e) {
                        console.error(e);
                    }
                },
            });
        } else {
            try {
                await followTimeline(id);
                changeListStates({ follow: true });
                message({
                    content: (
                        <div style={{ textAlign: 'center' }}>
                            追踪成功<br />在我-【消息】中收取事件最新进展
                        </div>
                    ),
                });
            } catch (e) {
                console.error(e);
            }
        }
    };

    const barRef = useRef();
    const navBgRef = useRef();
    const [barHeight, setBarHeight] = useState(0);
    const [navBgHeight, setNavBgHeight] = useState(0);

    const showFixedTitle = useFixedTitle(navBgHeight);

    useEffect(
        () => {
            if (barRef.current) {
                setBarHeight(barRef.current.offsetHeight);
                changeListStates({ navHeight: barRef.current.offsetHeight });
            }
            if (navBgRef.current) {
                setNavBgHeight(navBgRef.current.offsetHeight);
            }
        },
        [statusBarHeight],
    );

    const [showMore, setShowMore] = useState(false);

    const closeMore = () => {
        setShowMore(false);
    };

    const handleMoreClick = () => {
        setShowMore(true);
    };

    const handleBackClick = () => {
        groundsClose();
    };

    return (
        <>
            {isGrounds ? (
                <div className={styles.barWrap} ref={barRef}>
                    <StatusBar statusBarHeight={statusBarHeight} />
                    <div className={styles.bar}>
                        <div className={styles.back} onClick={handleBackClick} />
                        <div className={styles.barTitle} style={{ opacity: showFixedTitle ? 1 : 0 }}>
                            #{title}
                        </div>
                        <div className={cx('follow', { followed: follow })} onClick={handleFollowClick}>
                            <div className={styles.followText}>{follow ? '已追踪' : '追踪'}</div>
                        </div>
                        <div className={styles.more} onClick={handleMoreClick} />
                        <Sheet show={showMore} onClose={closeMore} dragToClose={false}>
                            <More close={closeMore} />
                        </Sheet>
                    </div>
                </div>
            ) : null}

            <div
                className={`${styles.navBg} ${!isGrounds ? styles.navBgH5 : ''}`}
                style={{
                    paddingTop: barHeight,
                    marginTop: -barHeight,
                }}
                ref={navBgRef}>
                <div className={styles.title}>#{title}</div>
            </div>

            <div
                className={cx('backToTop', { backToTopShow: showFixedTitle })}
                onClick={() => {
                    smoothScroll(window, 0);
                }}
            />
        </>
    );
};

export default errorBoundary(Nav);
