import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import errorBoundary from '@src/components/errorBoundary';
import { TL_NODE_ENV } from '@src/public/constants';
import { isGrounds } from '@src/public/utils/app';
import { groundsDispatch } from './utils';
import styles from './index.css';

const Link = ({ children, to, type = 'web', target, className = '', style = {}, needLinkAnalyze = false }) => {
    const lockRef = useRef(false);

    if (isGrounds) {
        const handleSpanClick = () => {
            if (!lockRef.current) {
                lockRef.current = true;
                groundsDispatch({ to, type, needLinkAnalyze }).finally(() => {
                    lockRef.current = false;
                });
            }
        };

        return (
            <span onClick={handleSpanClick} className={classNames(styles.span, className)} style={style}>
                {children}
            </span>
        );
    } else {
        return (
            <a href={to} {...(target ? { target } : {})} className={classNames(styles.a, className)} style={style}>
                {children}
            </a>
        );
    }
};

Link.propTypes = {
    children: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.array]),
    to: PropTypes.string,
    type: PropTypes.string,
    target: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.object,
    needLinkAnalyze: PropTypes.bool,
};

const pathBasePc = TL_NODE_ENV === 'development' ? '/pc/news' : '';
const pathBaseH5 = TL_NODE_ENV === 'development' ? '/mobile/ishare' : '';
const pathBaseClient = TL_NODE_ENV === 'development' ? '/mobile/timeline' : '';
const pathBase = isGrounds ? pathBaseClient : pathBaseH5;
const origin = isGrounds
    ? window.location.origin.replace('ishare', 'timeline')
    : window.location.origin.replace('timeline', 'ishare');

export const linkBasePc = `${window.location.origin}${pathBasePc}`;

export const linkBase = `${origin}${pathBase}`;

// 端内方法形式的跳转
export const dispatchPage = ({ to, type = 'web', needLinkAnalyze = false }) => {
    groundsDispatch({ to, type, needLinkAnalyze });
};

export default errorBoundary(Link);
