import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';
import errorBoundary from '@src/components/errorBoundary';
import { formatNodeTimeEnum } from '@src/public/utils/format';
import { EVENT_LIST_INFLOW } from '@src/public/constants';
import EventCard from '@src/mobile/components/EventCard';
import styles from './index.css';

const cx = classNames.bind(styles);

const Item = ({
    content,
    showType,
    allFold,
    parties,
    timeline,
    changeFold,
    index,
    scrollToNode,
    changeScrollToNode,
}) => {
    const { id, nodeTime, timeType, partyId, highlights } = content;
    const isInFlow = showType === EVENT_LIST_INFLOW;

    // 决定当前短内容收起展开的变量
    const [fold, setFold] = useState(true);

    // 当外部触发全部收起/展开时，强制当前短内容收起/展开
    useEffect(
        () => {
            setFold(allFold);
        },
        [allFold],
    );

    // 改变当前短内容收起展开状态
    const changeItemFold = bool => {
        setFold(bool);
        changeFold(id, bool);
    };

    // 点击当前短内容的收起按钮
    const handleFoldBtnClick = () => {
        changeItemFold(true);
    };

    // 点击收起状态的当前短内容
    const handleFoldWrapClick = () => {
        // 如果是嵌入热点专题或者未处于收起状态，直接返回
        if (isInFlow || !fold) {
            return;
        }

        changeItemFold(false);
    };

    const isHighLight = highlights === 1;
    const party = parties.find(item => item.id === partyId);
    const partyColor = isInFlow ? '#F54343' : party.color;
    const cardBgColor = !isInFlow && isHighLight ? '#3091f51A' : '#FFFFFF';

    const itemRef = useRef();
    const [showBlink, setShowBlink] = useState(false);

    useEffect(
        () => {
            if (scrollToNode) {
                itemRef.current.scrollIntoView({ block: 'center' });
                setShowBlink(true);
                changeScrollToNode(false);
            }
        },
        [scrollToNode],
    );

    useEffect(
        () => {
            if (showBlink) {
                const timer = setTimeout(() => {
                    setShowBlink(false);
                }, 2000);

                return () => {
                    clearTimeout(timer);
                };
            }
        },
        [showBlink],
    );

    return (
        <div className={styles.item} ref={itemRef}>
            <div className={styles.left}>
                <div className={styles.pointWrap}>
                    <div
                        className={cx('point', isHighLight ? 'pointHighlight' : 'pointNormal')}
                        style={isHighLight ? { color: partyColor } : { backgroundColor: partyColor }}
                    />
                </div>
                <div className={styles.line} />
            </div>
            <div className={styles.content}>
                <div className={styles.nav}>
                    <div className={styles.time}>{formatNodeTimeEnum(nodeTime, timeType)}</div>
                    {fold || isInFlow ? null : (
                        <div className={styles.foldBtn} onClick={handleFoldBtnClick}>
                            收起
                        </div>
                    )}
                </div>
                <div className={styles.cardWrap} onClick={handleFoldWrapClick}>
                    <EventCard
                        content={{ ...content, cardBgColor, showBlink }}
                        timeline={timeline}
                        showType={showType}
                        fold={fold}
                        index={index}
                    />
                </div>
            </div>
        </div>
    );
};

Item.propTypes = {
    content: PropTypes.object,
    showType: PropTypes.string,
    allFold: PropTypes.bool,
    parties: PropTypes.array,
    timeline: PropTypes.object,
    changeFold: PropTypes.func,
    index: PropTypes.number,
    scrollToNode: PropTypes.bool,
    changeScrollToNode: PropTypes.func,
};

export default errorBoundary(Item);
