import axios from 'axios';
import { COMMENT_BASE_PATH, COMMENT_APP_ID } from '@src/public/constants';
import { getToken } from '@src/public/utils/app';

// 评论axios实例
export const request = axios.create();

// 评论axios实例的基础路径
request.defaults.baseURL = COMMENT_BASE_PATH;

// 评论axios实例的请求拦截器
request.interceptors.request.use(
    (config) => {
        // 获取用户token
        const token = getToken();
        // 评论axios实例的自定义header
        const headers = {
            Authorization: `Bearer ${token}`,
            commentAppId: COMMENT_APP_ID,
        };

        return { ...config, headers: { ...config.headers, ...headers } };
    },
    (err) => Promise.reject(err),
);

