/**
 * 给评论点赞
 * @param {string} commentId 评论id
 * @param {string} opType INSERT/DEL 点赞/取消点赞
 * @param {string} subjectId 主体id
 */
export const postLikeComment = ({ commentId, opType, subjectId }) => {
    return request.post('/likeComment', {
        body: {
            commentId,
            appId: COMMENT_APP_ID,
            subjectId,
        },
        opType,
    });
};

/**
 * 删除评论
 * @param {string} commentId 评论id
 * @param {string} subjectId 主体id
 */
export const postDeleteComment = ({ commentId, subjectId }) => {
    return request.post('/delComment', {
        body: {
            commentId,
            subjectId,
            appId: COMMENT_APP_ID,
        },
    });
};

/**
 * 插入评论
 * @param {string} subjectId 主体id
 * @param {string} comment 评论内容
 * @param {string} rootId 根id
 * @param {string} pId 父id
 */
export const postInsertComment = ({ subjectId, comment, rootId, pId }) => {
    return request.post('/insertComment', {
        body: {
            subjectId,
            comment,
            rootId,
            pId,
            appId: COMMENT_APP_ID,
        },
    });
};

/**
 * 获取单条评论数据
 * @param {string} commentId 评论id
 * @param {string} subjectId 主体id
 */
export const getCommentById = async ({ commentId, subjectId }) => {
    return request.get('/queryCommentDetail', {
        params: {
            id: commentId,
            subjectId,
            appId: COMMENT_APP_ID,
        },
    });
};
